import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import { IOrderPageImg } from '../../models/IOrderPageImg';

interface OrderPageImgsState {
  orderPageImgs: IOrderPageImg[];
  isLoading: boolean;
  error: string;
}

const initialState: OrderPageImgsState = {
  orderPageImgs: [],
  isLoading: false,
  error: '',
};

export const orderPageImgsSlice = createSlice({
  name: 'orderPageImgs',
  initialState,
  reducers: {
    orderPageImgFetching(state) {
      state.isLoading = true;
    },
    orderPageImgFetchingSuccess(state, action: PayloadAction<IOrderPageImg[]>) {
      state.isLoading = false;
      state.error = '';
      state.orderPageImgs = action.payload;
    },
    orderPageImgFetchingError(state, action: PayloadAction<string>) {
      state.isLoading = false;
      state.error = action.payload;
    },
  },
});

export default orderPageImgsSlice.reducer;
